section {
	width: 90%;
	max-width: 800px;
	margin: 0 auto 50px auto;
}

h1 {
	font-family: 'Volkhov', serif;
}

.accordion {
	.accordion-list {
		padding: 0;
		margin: 0;
		list-style-type: none;
	}

	.accordion-item {
		color: white;
	}

	.accordion-button {
		background-color: black;
		color: white;
		width: 100%;
		border: 1px solid gray;
		border-bottom: none;
		cursor: pointer;
		font-size: 16px;
		padding: 10px 0;
	}

	.accordion-text {
		display: none;
		background-color: white;
		color: black;
		border: 1px solid black;
		border-top: none;
		padding: 15px;
		background-color: #f5f5dc;
	}
}

.tabs {
	.tab-list {
		list-style-type: none;
		padding: 0;
		margin: 0;
	}

	.tab-item {
		display: inline-block;
	}

	.tab-link {
		display: block;
		padding: 5px 15px;
		background-color: black;
		color: white;
		text-decoration: none;
		border: 1px solid black;
		border-bottom: none;

		&.active {
			background-color: #f5f5dc;
			color: black;
			border: 1px solid #b9b9b9;
			border-bottom: none;
		}
	}

	.tab-content {
		background-color: #f5f5dc;
		padding: 15px;
		border: 1px solid #b9b9b9;
		display: none;

		&.active {
			display: block;
		}
	}
}

.modal-container {
	.modal-bg {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.7);
	}

	.modal {
		margin: 0 auto;
		padding: 20px;
		background-color: #0b0625;
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		border: 3px solid #ffffff;
		box-shadow: 0 0 10px #000000;
		z-index: 100;
	}

	.modal-content {
		margin: 0 auto;
	}

	.modal-close {
		background-color: #317399;
		color: #ffffff;
		margin: 10px auto 0 auto;
		display: block;
		cursor: pointer;
		padding: 10px;
		border: none;
		border-radius: 15px;
		border: 2px solid #ffffff;

		&:hover {
			background-color: #317399 - 30;
		}
	}

	.modal-btn {
		border-radius: 8px;
		padding: 10px;
		border: none;
		background-color: #f5f5dc;
		border: 1px solid #959595;
		cursor: pointer;

		&:hover {
			background-color: #dddddd;
		}
	}
}
